<template>
    <div>
         <el-button type="primary" calss="creat" @click="update_text" class="button-margin"> 未终结作业 </el-button>
        <el-input  v-model="text"    clearable  type="textarea" style="font-size: larger;" :rows="rows"></el-input>
    </div>
  
</template>

<script>
import * as analyse from '../read-analyse'
import * as tools from '../tool'
export default {
    name: "NotDone",
    data() {
        return {
            rows : 8,
            text : "无",
            plan_count: 0 
        }
    },
    methods: {
        update_text () {
            this.text = analyse.get_not_done("\n")
            window.not_done = this.text
            this.$notify({
          title: '成功',
          message: '未终结作业已分析完毕',
          type: 'success'
        });
        }
    },
    watch: {
        text(newDate, oldDate){
            window.not_done = newDate
        }
    }
}
</script>

<style>

</style>


