function get_day() {
    var d = new Date()
    return d.format("MM月dd日")
}

function get_temmorow() {
    var d = new Date()
    var dateTime = d.setDate(d.getDate() + 1)
    var t = new Date(dateTime)
    return t.format("MM月dd日")
}

function number_c(number) {
    var number_list = ["零", "一", "二", "三", "四", "五", "六", "七", "八", "九", "十"]
    return number_list[number]
}

function have_elements(str, elements, count = 3) {
    var res = elements.filter((e) => {
        return (str.indexOf(e) != -1)
    })
    return res.length >= count
}

