function split_m(text) {
    if (text.split("：").length > 1) {
        var result = text.split("：")
        if (result[1].split(":").length > 1 || result[0].split(":").length > 1) {   //处理预计结束时间：22:00的情况
            var result = [...result[0].split(":"), ...result[1].split(":")]
        }
    } else {
        var result = text.split(":")
    }
    result.shift()
    if (result.length == 1) {
        return result[0]
    }
    return result.join("：")
}
function isNumber(val) {

    var regPos = /^[0-9]+.?[0-9]*/; //判断是否是数字。

    if (regPos.test(val)) {
        return true;
    } else {
        return false;
    }

}
function title_filter(title) {
    //去除序号，调整（1）(1)、1、统一为（1），（可接受）改为（可接受风险）
    // var title = "1、title 4、（1）(1)、1、"
    if (1 <= title.indexOf("、") && title.indexOf("、") <= 3) {
        title = title.slice(title.indexOf("、") + 1,)
    }

    title = title.replaceAll("(", "（")
    title = title.replaceAll(")", "）")
    title = title.replaceAll("）、", "）")

    while (title.lastIndexOf("、") != -1) {
        var index = title.lastIndexOf("、")
        var number_len = 0
        if (isNumber(title[index - 1])) {
            number_len = 1
        }
        if (isNumber(title.slice(index - 2, index))) {
            number_len = 2
        }
        if (isNumber(title.slice(index - 3, index + 1))) {
            number_len = 3
        }
        if (number_len == 1 || number_len == 2) {
            title = title.slice(0, index - number_len) + "（" + title.slice(index - number_len, index) + "）" + title.slice(index + 1)
        } else {
            title = title.slice(0, index) + "...." + title.slice(index + 1)
        }
    }
    title = title.replaceAll("....", "、")
    if (title.indexOf("（可接受）") != -1) {
        title = title.replaceAll("（可接受）", "（可接受风险）")
    }
    return title
}
function clear_catch(site_names) {
    site_names.forEach(element => {
        localStorage.removeItem(element)
    });
}
function clear_yesterday_catch(site_names) {
    var is_yesterday = true
    site_names.forEach(element => {
        var content = localStorage.getItem(element)
        if (content) {
            var title = localStorage.getItem(element).split("\n")[0]
            if (title.indexOf(get_day()) != -1) {
                is_yesterday = false
            }
        }
    })
    if (is_yesterday) {
        console.log("检测到是不是今天的计划进行清除")
        clear_catch(site_names)
    }
}
window.c = clear_catch
