<template>
    <div id="div1" class="text">
            <!--可使用 min-height 实现编辑区域自动增加高度-->
            <p>此部分为自动生成的内容</p>
    </div>
</template>

<script>
import E from 'wangeditor'
import * as analyse from '../read-analyse'

export default {
    name: "ShowEdit1",
    data(){
        return {
            editor: null,
            name:"报告"
        }
    },
    methods: {
        set_report() {
            var report = analyse.get_report()
            this.editor.txt.html(report)
            if(report.indexOf("风险数量错误")!=-1){
                this.$notify.error({
                title: '错误',
                message: '请检查是否有未填写风险等级情况，或格式换行错误'
                });
            }else{
                this.$notify({
                title: '成功',
                message: '工作日报分析完成',
                type: 'success'
        });
            }
        }
    },
    mounted() {
        console.log("creat");
        this.editor = new E('#div1')
        const menus = [
        'head',
        'bold',
        'italic',
        'underline',
        'foreColor',
        'list',
        'justify',
        'undo',
        'redo'
        ]
        this.editor.config.menus = menus
        this.editor.config.height = 800  
        this.editor.config.zIndex = 5  
        this.editor.create()
        }
}
</script>
<style scoped>
#div1 {
    text-align: justify;
}
</style>


