import { TimeSelect } from 'element-ui';
import * as tools from './tool'
String.prototype.replaceAll = function (FindText, RepText) {
    //return this.replace(new RegExp(s1,"gm"),s2);
    return this.split(FindText).join(RepText);
}
function clear_txt(txt) {
    var l = []
    var txt = txt.replace("       ", "\n")
    var txt = txt.replace(" ", "")
    var txt = txt.replace(":", "：")
    var txt = txt.replace("?", "")
    var txt_l = txt.split("\n")
    for (var i in txt_l) {
        var t = txt_l[i]
        if (t.indexOf("闭环情况") != -1) {
            break
        }
        if (t.length - 1 == t.lastIndexOf("。")) {
            t = t.slice(0, t.length - 1)
        }
        if (txt_l[i] != "" && txt_l[i].indexOf("延误的操作") == -1 && ((txt_l[i].length) >= 3)) {
            l.push(t.trim())
        }
    }
    return l
}
function creat_site(t, name) {
    var today_pos = 0
    var tomorrow_pos = 0
    var end_pos = 0
    var site = new Site(name)
    var t_list = clear_txt(t) //清洗数据
    if (t_list.length <= 2) {
        return site
    }
    //确定位置
    for (const index in t_list) {
        if (t_list[index].indexOf("工作完成情况") != -1) {
            today_pos = index
        }
        else if (t_list[index].indexOf("工作计划") != -1) {
            tomorrow_pos = index
        }
        else if (t_list[index].indexOf("未终结的") != -1||t_list[index].indexOf("后开展的作业") != -1) {
            end_pos = parseInt(index)
        }
    }
    console.log("site_index", today_pos,tomorrow_pos,end_pos);
    //如果没有确定位置，换第二种方法
    // if (tomorrow_pos == 0) {
    //     for (const index in t_list) {
    //         if (t_list[index].indexOf("的作业") == -1) {
    //             var flag = t_list[index].indexOf(tools.get_day())
    //             var flag2 = t_list[index].indexOf(tools.get_temmorow())
    //             // console.log(flag, flag2, t_list[index], "flag");
    //             if (0 <= flag && flag <= 2) {
    //                 today_pos = index
    //             } else if (flag > 2) {
    //                 tomorrow_pos = index
    //             }
    //             if (0 <= flag2 && flag2 <= 2) {
    //                 today_pos = index
    //             } else if (flag2 > 2) {
    //                 tomorrow_pos = index
    //             }
    //         }
    //     }
    // }
    //解决没有未终结的情况
    if (end_pos == 0&&tomorrow_pos!=0) {
        end_pos = t_list.length
        t_list.push("还未终结的作业为：")
    }
    //创建站点对象
    var plan = 0
    for (var index in t_list) {
        if (t_list[index].indexOf("风险") != -1 || t_list[index].indexOf("）") != -1 || t_list[index].indexOf("kV") != -1) {
            console.log("index", index, today_pos, tomorrow_pos, end_pos);
            console.log(parseInt(today_pos) < parseInt(index) && parseInt(index) < parseInt(tomorrow_pos));
            console.log(parseInt(tomorrow_pos) < parseInt(index) && parseInt(index) < parseInt(end_pos));
            console.log(parseInt(index) > parseInt(end_pos)&&t_list[index].indexOf("作业任务")!= -1)
            if (parseInt(today_pos) < parseInt(index) && parseInt(index) < parseInt(tomorrow_pos)) {
                plan = new Plan(t_list[index])
                site.today_plan.push(plan)
            }
            else if (parseInt(tomorrow_pos) < parseInt(index) && parseInt(index) < parseInt(end_pos)) {
                plan = new Plan(t_list[index])
                site.tomorrow_plan.push(plan)
            }
            else if (parseInt(index) > parseInt(end_pos)&&t_list[index].indexOf("作业任务")!= -1) {
                plan = new NoDonePlan(t_list[index])
                site.end_plan.push(plan)
            }
            else {
                plan.other.push(t_list[index])
                plan.init_text()
            }
        }
        else if ((t_list[index].indexOf(":") != -1 || t_list[index].indexOf("：") != -1) && (tomorrow_pos != index && index != end_pos && index != today_pos)) {
            plan.other.push(t_list[index])
            plan.init_text()
        }
    }
    site.init_plan()
    return site
}
//creat_site(t)
class Site {
    constructor(name) {
        this.count = 0
        this.name = name
        this.today_plan = []
        this.tomorrow_plan = []
        this.end_plan = []
        this.permit_plan = []
    }
    init_plan() {
        var tomorrow = []
        var permit = []
        this.tomorrow_plan.forEach(element => {
            if (element.is_permission()) {
                element.clear_risk()
                element.init_text()
                permit.push(element)
            } else {
                element.clear_control()
                element.init_text()
                tomorrow.push(element)
            }
        });
        this.tomorrow_plan = tomorrow
        this.permit_plan = permit
        //去除今天作业中的许可
        var today = []
        this.today_plan.forEach(element => {
            if (element.is_permission()) {

            } else {
                today.push(element)
            }
        });
        this.today_plan = today
    }
    get_risk_count(p) {
        var count = 0

        this.tomorrow_plan.forEach(element => {
            if (element.get_risk() == p) {
                count += 1
            }
        });
        return count
    }
    get_operate_count() {
        var count = 0
        this.tomorrow_plan.forEach(element => {
            if (element.is_operate()) {
                count += 1
            }
        });
        return count
    }
    get_today_txt() {
        var list = []
        for (const index in this.today_plan) {
            list.push(this.today_plan[index].text)
        }
        return list.join("\n")
    }
    get_tomorrow_txt() {
        if (this.tomorrow_plan.length == 0) {
            return "无"
        }
        var list = []
        for (const index in this.tomorrow_plan) {
            list.push((parseInt(index) + 1) + "、" + this.tomorrow_plan[index].get_edit_text())
        }
        return list.join("<br>")
    }
    get_permit_txt() {
        if (this.permit_plan.length == 0) {
            return "无"
        }
        var list = []
        for (const index in this.permit_plan) {
            list.push((parseInt(index) + 1) + "、" + this.permit_plan[index].get_edit_text())
        }
        return list.join("<br>")
    }
    get_title() {
        return "（" + tools.number_c(this.index + 1) + "）" + this.name + "巡维中心" + tools.get_temmorow() + "工作计划："
    }
    get_permit_title() {
        return "（" + tools.number_c(this.index + 1) + "）" + this.name + "巡维中心" + tools.get_temmorow() + "许可作业计划："
    }
}
class NoDonePlan{
    constructor(content) {
        this.content = content
        this.other = []
        this.text = ""
        this.type = "no_done"
        this.content = tools.title_filter(this.content)
    }
    init_text() {
        var l = [this.content]
        // this.clear_other()
        this.clear_title()
        l.push(...this.other)
        this.text = l.join("\n")
        return this.text
    }
    get_edit_text() {
        return this.text.replaceAll("\n", "<br>")
    }
    clear_title() {
        var index = this.content.indexOf(")")
        if (index <0){
            var index = this.content.indexOf("）")
        }
        if (0<index&&index<5){
            this.content = this.content.slice(index+1,)
        }
    }
}
class Plan {
    constructor(content) {
        this.content = content
        this.other = []
        this.text = ""
        this.type = "done"
        this.content = tools.title_filter(this.content)
    }
    get_risk() {
        var result = "无"
        var l = ["可接受", "低风险", "中风险", "高风险"]
        l.forEach(element => {
            if (this.text.indexOf(element) != -1) {
                result = element
            }
        });
        //查看是否是风险等级换行了
        // if (result=="无") {
        //     l.forEach(element => {
        //         if (this.other[0].indexOf(element) != -1) {
        //             result = element
        //         }
        //     });
        //     if (result!="无") {
        //         this.content = this.content + this.other[0]
        //         this.other = this.other.slice(1,)
        //         this.content = tools.title_filter(this.content)
        //     }
        // }
        return result
    }
    is_operate() {
        var str_list = ["操作", "重合闸", "投退", "送电", "停电", "复电", "倒闸", "投入", "退出", "停复"]
        if (tools.have_elements(this.text, str_list, 2)) {
            console.log(this.text);
            return true
        }
        return false
    }
    is_permission() {
        if (this.other.length == 1 && this.other[0].indexOf("许可") != -1) {
            return true
        }
        return false
    }
    init_text() {
        var l = [this.content]
        this.clear_other()
        l.push(...this.other)
        this.text = l.join("\n")
        return this.text
    }
    get_edit_text() {
        return this.text.replaceAll("\n", "<br>")
    }
    clear_control() {
        //处理有工作负责人未写管控人的情况,没有标记0
        var control = ""
        var control_flag = 0
        this.other.forEach(element => {
            if (element.indexOf("工作负责人") != -1) {
                control = element.split("：")[1]
            }
            if (element.indexOf("管控") != -1) {
                control_flag = 1
            }
        });
        if (control_flag == 0) {
            this.other.push("现场管控人：" + control)
        }
        // console.log(control, control_flag);
    }
    clear_risk() {
        console.log(this.content, this.is_permission());
        if ((this.content.indexOf("风险") != -1) && this.is_permission()) {
            var end_index = this.content.lastIndexOf("（")
            if (end_index == -1) {
                end_index = this.content.lastIndexOf("(")
            }
            this.content = this.content.slice(0, end_index)
        }
    }
    clear_other() {
        var temp_other = []
        this.other.forEach(element => {
            if (element.indexOf("管控人") != -1 && element.indexOf("视频管控人") == -1) {
                var name = tools.split_m(element)
                temp_other.push("现场管控人" + "：" + name)
            } else if (element.indexOf("许可") != -1) {
                var name = tools.split_m(element)
                temp_other.push("工作许可人" + "：" + name)
            } else if (element.indexOf("预计结束时间") != -1) {
                var name = tools.split_m(element)
                if (name.indexOf("日") != -1) {
                    name = name.split("日")[1]
                }
                if (name.indexOf("：") != -1) {
                    name = name.split("：")[0] + "时" + name.split("：")[1] + "分"
                }
                console.log("name2", name);
                temp_other.push("预计结束时间" + "：" + name)
            } else {
                temp_other.push(element)
            }
        });
        this.other = temp_other
    }
}
function get_report(edit) {
    var edit_l = []
    var data = window.data
    var site_names = window.site_names
    var dict = function () { }
    dict.today_count = 0
    dict.not_done_count = 0
    dict.temorrow_count = 0
    dict.hight_count = 0
    dict.middle_count = 0
    dict.low_count = 0
    dict.acc_count = 0
    dict.operate_count = 0
    dict.permit_count = 0
    edit_l.push(tools.template_red("变电运行所工作日报"))
    edit_l.push(tools.template_red("一、变电运行所" + tools.get_day() + "作业完成情况："))
    for (const index in site_names) {
        var site_name = site_names[index]
        var site = data[site_name]
        if (site) {
            dict.today_count += site.today_plan.length
            dict.temorrow_count += site.tomorrow_plan.length
            dict.not_done_count += site.end_plan.length
            dict.permit_count += site.permit_plan.length
            dict.hight_count += site.get_risk_count("高风险")
            dict.middle_count += site.get_risk_count("中风险")
            dict.low_count += site.get_risk_count("低风险")
            dict.acc_count += site.get_risk_count("可接受")
            dict.operate_count += site.get_operate_count()
        }
    }
    edit_l.push(tools.template_p(get_head_one(dict)))
    if (dict.not_done_count != 0) {
        //如果未完成作业已生成，先读取缓存
        if (window.not_done) {
            var t = ""+window.not_done.replaceAll("\n", "<br>")
            edit_l.push(t)
        }else{
            edit_l.push(tools.template_red("未完成作业为："))
            edit_l.push(tools.template_p(get_not_done("<br>")))
        }
    }
    edit_l.push("")
    edit_l.push(tools.template_red(get_head_two(dict)))
    //数量不等时候做出提示
    if (dict.temorrow_count != (dict.hight_count + dict.middle_count + dict.low_count + dict.acc_count)) {
        edit_l.push(tools.template_red("○●风险数量错误●○"))
    }
    for (const index in site_names) {
        var site_name = site_names[index]
        var site = data[site_name]
        if (site) {
            edit_l.push(tools.template_red(site.get_title()))
            edit_l.push(tools.template_p(site.get_tomorrow_txt()))
        }
    }
    edit_l.push("")

    //如果许可作业为0项，不显示下面内容
    if (dict.permit_count != 0) {
        edit_l.push(tools.template_red(get_permit_title(dict)))
        for (const index in site_names) {
            var site_name = site_names[index]
            var site = data[site_name]
            if (site) {
                edit_l.push(tools.template_red(site.get_permit_title()))
                edit_l.push(tools.template_p(site.get_permit_txt()))
            }
        }
    }
    console.log(edit_l);
    return edit_l.join("<br>")
}
//获取所有未终结作业
function get_not_done(fuhao) {
    var result = []
    var done_index = 1
    var data = window.data
    var site_names = window.site_names
    for (const index in site_names) {
        var site_name = site_names[index]
        var site = data[site_name]
        console.log(site_name, site, "done");
        if (site) {
            var plans = site.end_plan
            console.log(site_name, plans, site);
            plans.forEach(element => {
                element.type = "not_done"
                if (fuhao == "\n") {
                    var t = element.text
                } else {
                    var t = element.get_edit_text()
                }
                // result.push(done_index + "、" + t)
                result.push("("+tools.number_c(done_index)+")"+t)
                done_index += 1
            });
        }
    }
    if (fuhao == "\n") {
        if (done_index==0) {
            var title = "截止当前变电运行所还未终结的工作" + (done_index - 1) + "项。"
        }else {
            var title = "截止当前变电运行所还未终结的工作" + (done_index - 1) + "项："
        }
        result = [title, ...result]
    }
    console.log(result, "not_done");
    return result.join(fuhao)
}
function get_head_one(dict) {
    var t = "变电运行所" + tools.get_day() + "作业计划共" + dict.today_count + "项。" + "已完成" + (dict.today_count - dict.not_done_count) + "项，未完成" + dict.not_done_count + "项，持续开展0项。"
    return t
}
function get_permit_title(dict) {
    return "三、变电运行所" + tools.get_temmorow() + "计划开展许可作业" + dict.permit_count + "项。"
}
function get_head_two(dict) {
    var t = "二、变电运行所" + tools.get_temmorow() + "计划开展作业" + dict.temorrow_count + "项。" + "其中，生产作业" + dict.temorrow_count + "项；倒闸操作" + dict.operate_count + "项。其中，高风险作业" + dict.hight_count + "项，中风险作业" + dict.middle_count + "项，低风险作业" + dict.low_count + "项，可接受风险" + dict.acc_count + "项。"
    return t
}
export {
    creat_site,
    get_report,
    get_not_done,
    Site
}


