// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import * as tools from './tool'
import animated from 'animate.css'

Vue.config.productionTip = false
window.tools = tools
import { Button, Select, Row, Col,Input,Tabs,TabPane,Backtop,Notification, Switch } from 'element-ui';
Vue.use(Button)
Vue.use(Select)
Vue.use(Row)
Vue.use(Col)
Vue.use(Input)
Vue.use(Tabs)
Vue.use(TabPane)
Vue.use(Backtop)
Vue.use(animated)
Vue.use(Switch)
Vue.prototype.$notify = Notification;
/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>'
})



