<template>
  <div id="app">
  <!-- <Title/> -->
    <img src="./assets/logo.png" class="animate__animated animate__tada">
    <div  style="margin-bottom: 20px;">工作日报自动汇总v2.1：更新未终结作业模版</div>
    <el-tabs v-model="activeName" type="border-card" @tab-click="handleClick">
      <el-tab-pane v-for="name in site_names" :key="name" :label="name" :name="name">
        <Esite  :key="name" :name="name" />
        </el-tab-pane>
    </el-tabs>
    <el-button @click="clear_catch" class="button-margin">清除缓存</el-button>
    <el-switch class="switch-button" v-model="open_add" active-text="开启自动添加视频管控人" ></el-switch>
    <NotDone/>
    <el-button @click="set_report" class="button-margin">汇总结果生成报告</el-button>
    <el-button @click="select_all" class="button-margin toTop">选中全部并复制</el-button>
    <ShowEdit1 ref="edit" />
    <el-backtop class="top-button" ><i class="el-icon-arrow-up"></i></el-backtop>
  </div>
</template>

<script>
import Esite from './components/e_site'
import ShowEdit1 from './components/ShowEdit1'
import  NotDone from './components/NotDone.vue'
import Title from './components/Title.vue'
import * as tool from './tool'

var open_add = localStorage.getItem("open_add")
if (open_add=="false") {
  window.open_add = false
}else {
  window.open_add = true
}
export default {
  name: 'App',
  components: {
    Esite,
    ShowEdit1,
    NotDone,
    Title
  },
  data() {

      return {
      site_names:  "贡山，兰坪，六库，崇仁，福贡".split("，"),
      activeName: "贡山",
      open_add: window.open_add,
    }
  },
  watch: {
    open_add(new_add, old_add){
      window.open_add = new_add
      localStorage.setItem("open_add", new_add)
    }
  },
  methods: {
      clear_catch() {
        tool.clear_catch(this.site_names)
        this.$notify({
          title: '成功',
          message: '已清除缓存，请手动刷新页面',
          type: 'success'
        });
      },
      handleClick(tab, event) {
        // console.log(tab, event);
      },
      set_report() {
        var result = this.$refs.edit.set_report()
      },
      select_all() {
        var t = document.querySelector(".w-e-text")
        var s = window.getSelection()
        s.selectAllChildren(t)
        var isCopyOk = document.execCommand('copy')
        if(isCopyOk) {
          this.$notify({
          title: '成功',
          message: '已将内容复制到粘贴板',
          type: 'success'
        });
        }else{
        this.$notify.error({
          title: '错误',
          message: '复制出现问题'
        });
        }
      }
    },
    mounted() {
    }
}
window.data = {}
window.site_names = "贡山，兰坪，六库，崇仁，福贡".split("，")
tool.clear_yesterday_catch(window.site_names)
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  max-width: 1000px;
  margin: auto;
}
.red {
  color:red
}
.button-margin {
  
  margin-top: 10px;
  margin-bottom: 10px;
  width: 300px;
}
.top-button {
  z-index: 2000;
}
.switch-button {
    border: 1px solid #DCDFE6;
    padding: 3px;
    margin: 15px;
    width: 260px;
    padding: 10px 20px;
}
</style>



