<template>
    <div class="slide-inner">
        <h2 class="maintitle">变电运行所<br>每日报表自动生成</h2>
        <!-- <p class="subtitle">作者：cj  版本：1.2</p> -->
    </div>
</template>
<script>
export default {
    name: "Title",
}
</script>
<style scoped>
.maintitle {
    position: relative;
    display: inline-block;
    font-size: 40px;
    line-height: 67px;
    font-weight: 700;
    padding-bottom: 10px;
}
.maintitle:after {
    content: "";
    position: absolute;
    left: 50%;
    bottom: 0;
    width: 200px;
    height: 3px;
    margin-left: -100px;
    background-color: #d65050;
}
.subtitle {
    font-size: 30px;
    line-height: 40px;
    /* padding: 20px 0 50px; */
    font-weight: 300;
}
.slide-inner {
    /* position: absolute;
    top: 50%; */
    left: 0;
    right: 0;
    text-align: center;
    z-index: 2;
    width: 100%;
    min-height: 260px;
    padding-top: 5px;
    /* -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    transform: translateY(-50%); */
}
</style>


