<template>
    <div class="site-input">
        <div>{{title}}</div>
        <el-input  type="textarea" style="font-size: larger;" @input="click_creat" :rows="17" autosizeplaceholder="请输入信息" v-model="content"></el-input>
        <el-button type="primary" calss="creat" @click="click_creat" class="button-margin"> 生成结果 </el-button>
        <el-button type="danger" v-if="showDelete" @click="delete_content" class="button-margin">删除</el-button>
        <el-row  v-for="(plan,index) in site.tomorrow_plan" :key="index">
            <el-input  v-model="plan.text"     clearable  type="textarea"  :rows="4" class="input-content animate__animated animate__bounceInRight"></el-input>
            <!-- <el-button type="danger">删除</el-button> -->
        </el-row>
    </div>
</template>
<script>
import * as analyse from '../read-analyse'
import * as tool from '../tool'
export default {
    name: 'ESite',
    props: ["name"],
    data() {
        return {
            title: this.name+"巡维中心 将标准的日报粘贴于下方框内",
            content: "",
            site: new analyse.Site(this.name),
            showDelete: false,
        }
    },
    methods: {
        click_creat() {
            this.site = analyse.creat_site(this.content,this.name)
            this.site.name = this.name
            this.site.index = window.site_names.indexOf(this.name)
            window.data[this.name] = this.site
            console.log(this.site);
            if (this.content.length>=6) {
                this.showDelete = true
            }else{
                this.showDelete = false
            }
            localStorage.setItem(this.name,this.content)
        },
        get_text (plan) {
            return plan.get_text()
        },
        input_text(e) {
            e.target.value = "ceshi"
            console.log(e);
        },
        delete_content() {
            this.content = ""
            this.click_creat()
        }
    },
    mounted() {
        console.log("this.name", this.name);
        this.site.name = this.name
        this.site.index = window.site_names.indexOf(this.name)
        window.data[this.name] = this.site
        this.content = localStorage.getItem(this.name)
        if (this.content) {
            this.click_creat()
        }
    },
}

</script>
<style >
.site-input {
    margin: auto;
}
.creat {
    margin: 10px;
}
.input-content {
    font-size: larger;
    margin: 5px ;
}
</style>


